<template>
    <div id="app">
        <router-view/>
    </div>
</template>

<script>
import {computed, watch} from '@vue/composition-api';

export default {
    setup(props, {root} = context) {
        const message = computed(() => root.$store.state.message.message);
        watch(message, value => {
            if (typeof value !== 'undefined' && value !== '') {
                root.$helpers.alertToast(root, root.$store.state.message.type, value);
                root.$store.commit('message/setMessage', '');
            }
        });
    }
}
</script>

