export default {
	alertToast(context, method, message) {
		context.$swal({
			title: message,
			icon: method,
			position: "top-end",
			showConfirmButton: false,
			toast: true,
			timer: 3000
		});
	},

	alertMessage(context, method, message) {
		context.$swal({
			title: message,
			icon: method,
			timer: 2000
		});
	},

	alertConfirm(context, message, callback) {
		context.$swal({
			title: message,
			icon: 'question',
			showCancelButton: true,
			confirmButtonText: "Đồng ý",
			cancelButtonText: "Hủy",
			confirmButtonColor: "#3085d6",
			cancelButtonColor: "#d33",
			showCloseButton: true
		}).then(result => {
			if (result.value) {
				callback();
			}
		});
	},
	formatCurrency(num) {
		num = num.toString().replace(/\$|\./g, '');
		if (isNaN(num) || num.length > 18)
			num = "0";
		var sign = (num == (num = Math.abs(num)));
		num = Math.floor(num * 100 + 0.50000000001);
		num = Math.floor(num / 100).toString();
		for (var i = 0; i < Math.floor((num.length - (1 + i)) / 3); i++)
			num = num.substring(0, num.length - (4 * i + 3)) + '.' +
				num.substring(num.length - (4 * i + 3));
		return (((sign) ? '' : '-') + num);
	},
	formatHumanPrice(price) {
		let intPrice = parseFloat(price.split('.').join(''));
		let finalPrice = '';
		if (intPrice >= 1000000000) {
			finalPrice = Math.round((intPrice / 1000000000) * 100) / 100 + ' tỷ';
		} else if (intPrice >= 1000000) {
			finalPrice = Math.round((intPrice / 1000000) * 100) / 100 + ' triệu';
		} else {
			finalPrice = price + ' vnđ';
		}
		return finalPrice;
	},
	currentDate() {
		let today = new Date();
		let dd = String(today.getDate()).padStart(2, "0");
		let mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
		let yyyy = today.getFullYear();
		return yyyy + "-" + mm + "-" + dd;
	},
	sending(currentBtn, text = 'Đang gửi') {
		currentBtn.innerHTML = text + '<i class="far fa-spinner fa-spin"></i>';
		currentBtn.setAttribute("disabled", "disabled");
	},
	sendSuccess(currentBtn, text) {
		currentBtn.innerHTML = text;
		currentBtn.removeAttribute('disabled');
	},
	parseHTML(html) {
		let domElement = document.createElement('template');
		domElement.innerHTML = html;
		return domElement.content.cloneNode(true);
	}
}
