export const state = () => ({
	message: '',
	type: '',
});

export const mutations = {
	setMessage(state, data) {
		if (typeof data === 'string') {
			state.message = data;
			state.type = 'success';
			return;
		}
		state.message = data.message;
		state.type = data.type || 'success';
	}
};

export const namespaced = true;
